@import "akrom.general.less";
// Akrom Media.Queries  Files
@media only screen and (min-width: 1366px) {
  /*top*/
  .header {
    .header__logo {
      left: 71px;
    }
    .header__nav {
      li {
        a {
          letter-spacing: 1px;
          line-height: 82px;
        }
      }
    }
    .header__right {
      right: 0px;
      top: 26px;
    }
    .searchicon {
      margin-top: 3px;
    }
    .login {
      margin-left: 29px;
      top: 0px;
    }

    .lang {
      margin-left: 25px;
      a {
        margin: 0 0 0 4px;
      }
    }
  }
  .is-nottop {
    .header {
      .header__nav {
        left: 152px;
      }
    }
  }

  .page {
    padding-left: 0 !important;
    width: 1280px;
    margin: 0 auto !important;
  }
  // SearchWide


  .search {
    border-radius: 8px;
    .search__switcher {
      .clearfix();
      height: auto;
      border-radius: 8px 8px 0 0;
      overflow: hidden;
      &:before {
        display: none;
      }
      li {
        width: 50%;
        float: left;
        display: table;
        height: 88px;
        .search__switcher1,
        .search__switcher2 {
          font-size: 22px;
          position: static;
          float: none;
          display: table-cell;
          vertical-align: middle;
          line-height: 1.2;
          width: 100%;
          border-radius: 0;
          padding-top: 0;
        }
      }
    }
    .search__filters {
      .search__filter:nth-of-type(3) {
        .search__categ li {
          width: 50%;
          margin-right: 0;
        }
      }
    }
  }

  .search.search_wide {
    padding: 25px 0 35px;
    margin-bottom: 58px;
    .search__input {
      padding: 0px 72px 0 69px;
      margin-bottom: 23px;
      input {
        padding: 2px 0 0px 21px;
        height: 34px;
        font-size: 16px;
        font-family: mr, sans-serif;
        display: block;
        width: 100%;
        margin-right: -34px;
        float: left;
        line-height: 1;

      }
      .search__inputicon {
        width: 34px;
        position: static;
        background-color: #BCBDC0;
      }
    }
    .search__switcher {
      border-radius: 0;
      li {
        height: 101px;
        .search__switcher1,
        .search__switcher2 {
          padding-top: 8px;
          font-size: 26px;
        }
      }
    }
    .search__filters {
      padding: 0 3.5% 0 5.66%;
    }
    .search__filter {
      margin-top: 32px;
      padding-top: 9px;
      padding-left: 0;
      margin-right: 2.16%;
      &.search__filter1,
      &.search__filter2,
      &.search__filter4,
      &.search__filter5 {
        //        width: 188px;
        width: 14.88%;
        padding-right: 0.5%;
        margin-left: 0;

      }
      &.search__filter5 {
        margin-right: 0;
      }
      &.search__filter3 {
        width: 31.84%;
      }

      .search__title {
        font-size: 21px;
        margin-bottom: 6px;
      }
      .search__options {
        font-size: 16px;
        line-height: 1.2;

        span {
          overflow: hidden;
          display: block;
        }

        input {
          float: left;
          margin-left: 0;
          margin-right: 12px;
          margin-top: 4px;
        }
      }
    }

    .search__reset,
    .search__pdf {
      text-transform: uppercase;
      &.search__reset {
        padding-top: 3px;
        padding-right: 18px;
        margin-bottom: 20px;
      }
      &.search__pdf {
        padding-right: 18px;
        i {
          margin-top: -3px;
        }
        span {
          padding-bottom: 0;
          line-height: 1.3;
        }
      }
    }
    .catslider {
      position: relative;
      margin-top: 27px;
      .catslider__in {
        .search__categ {
          width: 72%;
          margin: 0 0 0 38px;
          .cycle-slide {
            text-align: center;
            margin-right: 20px;
            .cat_item {
              i {
                margin-bottom: 9px;
              }
              span {
                text-transform: lowercase;
                font-size: 16px;
                padding-top: 0;
              }
            }
          }
        }
      }
      .catslider__prev {
        background: url('../img/icons-s51a623a2b8.png') 0 -713px no-repeat;
        width: 20px;
        height: 37px;
        position: absolute;
        top: 50%;
        margin-top: -18.5px;
        left: 0;
        cursor: pointer;
      }
      .catslider__next {
        background: url('../img/icons-s51a623a2b8.png') 0 -478px no-repeat;
        width: 21px;
        height: 37px;
        position: absolute;
        right: 29px;
        cursor: pointer;
        top: 50%;
        margin-top: -18.5px;
      }
    }
  }

  //\\END SearchWide

  // Wine
  .wine {
    padding: 0;
    margin-top: 0;
    margin-bottom: 19px;
    margin-right: 0;
    height: 225px;
    .wine__pic {
      vertical-align: middle;
      //      width: 215px;
      width: 15.48%;
      img {
        margin-top: 7px;
        margin-bottom: 7px;
      }
    }
    .wine__text {
      //      width: 380px;
      width: 27%;
      padding-top: 23px;
      //      margin-right: 52px;
      margin-right: 3.8%;
      h2 {
        line-height: 36px;
        padding-right: 50px;
        margin-bottom: 14px;
      }
    }
    .wine__from {
      line-height: 19px;
      padding-top: 27px;
      //      width:110px;
      width: 8.0%;
      margin-right: 7.5%;
      padding-right: 0;
    }
    .wine__type {
      padding-top: 25px;
      padding-right: 0;
      margin-right: 83px;
      width: 13.7%;
      .wine__color {
        line-height: 19px;
        padding-top: 1px;
        margin-bottom: 20px;
        i {
          margin-right: 15px;
        }
      }
      p {
        margin-bottom: 18px;
        line-height: 20px;
        word-spacing: -1px;
      }
    }
    .wine_inf_price {
      padding-top: 27px;
      float: left;
      width: 16%;
      position: relative;
    }
    .wine__inf {
      margin-left: 13px;
      font-size: 12px;
      line-height: 14px;
    }
    .wine__price {
      bottom: auto;
      right: auto;
      left: 0;
      top: 157px;
    }
  }
  //\\ END Wine
}
