  .cat_item {
    text-align: center;
    width: auto !important;
    span {
      position: static !important;
      display: block !important;
      background: none !important;
      margin-left: 0 !important;
      width: auto !important;
      font-size: 14px;
      color: #808080 !important;
      &:before {
        display: none !important;
      }
    }
  }

  .offer {
    .offer__text {
      padding: 0 25px 0 36px;
    }
    .offer__year {
      font-size: 37px;
      padding-top: 21px;
    }
    .offer__title {
      font-size: 44px;
    }
    .offer__more {
      font-size: 21px;
    }
  }

  .entry.entry_big {
    .entry__text {
      padding: 33px 40px 69px 30px;
      .entry__title {
        font-size: 30px;
      }
    }
  }

  // Slider
  .superslider {
    height: 89.9vh;
    .superslider__text {
      padding: 0 71px;
    }
  }

  .superslider.center {
    margin: 0 auto;
    height: 89.9vh;
    .superslider__next {
      right: 30px;
    }
    .superslider__playpause {
      right: 30px;
    }
    .superslider__playpause {
      right: 30px;
    }
    .superslider__pagi {
      right: 50px;
    }
  }


  // Logoslider
  .logoslider.center {
    margin-bottom: 29px;
    margin-top: 24px;
    .logoslider__next,
    .logoslider__prev {
      top: 50%;
    }
    img {
      margin: 0 30px 0 0;
    }
  }
  // Logoslider End

  //\\Slider

  .footer {
    .footer__in {
      padding: 65px 0 87px 0;
    }
    .footer__nav {
      left: 101px;
      font-size: 21px;
      ul {
        line-height: 24px;
        position: relative;
        top: -4px;
        li {
          display: inline-block;
          width: 100%;
        }
      }
    }
    .footer__contacts {
      left: 98px;
      padding-left: 19px;
      font-size: 14px;
      h4 {
        font-size: 21px;
        line-height: 0.8;
        margin-bottom: 11px;
      }
      p {
        line-height: 1.25;
      }
    }
  }
  .warning {
    background-size: auto;
    max-width: none;

  }
