// Akrom General styles File
@font-face {
  font-family: mr;
  src: url("fonts/MyriadPro-Light.eot");
  src: url("fonts/MyriadPro-Light.eot?#iefix") format("embedded-opentype"),
       url("fonts/MyriadPro-Light.woff") format("woff"),
       url("fonts/MyriadPro-Light.ttf") format("truetype");
  font-weight: 100;
  font-style: normal;
}
* {
  box-sizing: border-box;
}
@topbar: 77px;
@transparent_black: rgba(0,0,0,0.8);
@h1_color: #B2004D;
h1,h2,h3,h4,h5 {
  font-weight: 100;
}

article {
}
.std_content_paddings {
  padding-left: 10.15%;
padding-right: 10.15%;
  padding-top: 4.2%;
}
.gradiente_block {
  background-image: -moz-linear-gradient(#ffffff, #e6e7e8);
  background-image: -webkit-linear-gradient(#ffffff, #e6e7e8);
  background-image: linear-gradient(#ffffff, #e6e7e8);
  box-shadow: 0 5px 14px rgba(0, 0, 0, 0.4);
  border: 1px solid #B2B2B2;
  border-radius: 6px;
}

.orange_border {
  position: relative;
  &:before,&:after {
    position: absolute;
    height: 5px;
    width: 100%;
    top: 0;
    left: 0;
    content: "";
    background: url(../img/icons-s51a623a2b8.png) 0 -1074px;
  }
  &:after {
    bottom: 0;
    top: auto;
  }

}

.clearfix{
  zoom:1;
  &:before, &:after{ content:""; display:table; }
  &:after{ clear: both; }
}

.pr0 {
  padding-right: 0;
}

input[type="radio"] {
  text-indent: -9999px;
  position: relative;
  width: 14px;
  height: 14px;
  &:before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    background: #ffffff;
    border-radius: 50%;
  }
  &:after {
    border-radius: 50%;
  }
}

.h2 {
  font-size: 15px;
  padding-left: 70px;
  height: 58px;
  line-height: 58px;
}

.btn, .header .login {
  padding: 0 24px;
  height: 21px;
  text-decoration: none;
  line-height: 22px;
}

.cat_item {
  text-align: center;
  width: auto !important;
  span {
    position: static !important;
    display: block !important;
    background: none !important;
    margin-left: 0 !important;
    width: auto !important;
    font-size: 14px;
    color: #808080 !important;
    &:before {
      display: none !important;
    }
  }
}

.offer {
  .offer__text {
    padding: 0 25px 0 36px;
  }
  .offer__year {
    font-size: 37px;
    padding-top: 21px;
  }
  .offer__title {
    font-size: 44px;
  }
  .offer__more {
    font-size: 21px;
  }
}

.entry.entry_big {
  .entry__text {
    padding: 33px 40px 69px 30px;
    .entry__title {
      font-size: 30px;
    }
  }
}

// Slider
.superslider {
  height: 89.9vh;
  .superslider__text {
    padding: 0 71px;
  }
}

// Logoslider

.logoslider {
  margin-top: 24px;
  img {
    margin: 0 30px 0 0;
  }
}


//////////////Catalog Page


.filters {
  margin-bottom: 26px;
  padding-left: 57px;
  i {
    vertical-align: top;
    margin-top: 2px;
  }
}
.view {
  top: -6px;
  a {
    margin-left: 21px;
    padding: 0 32px;
    line-height: 40px;
  }
}

.catalog {
  margin-bottom: 61px;
  .more {
    padding-top: 0;
    margin-top: 7px;
    margin-right: 45px;
  }
}

// Choosed List

.choosed_list_container {
  position: fixed;
  bottom: 0;
  right: 90px;
  z-index: 100;
  background-color: #000000;
  -webkit-border-radius: 12px 12px 0 0;
  -moz-border-radius: 12px 12px 0 0;
  border-radius: 12px 12px 0 0;
  width: 700px;
  padding: 0 12px 0 10px;
  &.collapsed {
    .choosed_list_content {
      display: none;
    }
  }
  .choosed_list_header{
    padding: 17px 0 12px;
    .heading {
      color: #E5E5E5;
      font-size: 16px;
      display: inline-block;
      margin-left: 47px;
      word-spacing: 2px;
      float: left;
    }
    .actions {
      float: right;
      margin-right: 22px;
      margin-top: 2px;
      a {
        display: inline-block;
        width: 20px;
        height: 14px;
        border-style: solid;
        border-width: 1px;
        margin-left: 27px;
        &.action_collapse {
          border-width: 0;
          border-bottom-width: 3px;
          border-bottom-color: #E5E5E5;
        }
        &.action_show {
          border-color: #E5E5E5;
          border-top-width: 3px;
        }
      }
    }
  }
  .choosed_list_content {
    background-color: #ffffff;
    padding: 30px 42px 30px 45px;
    .choosed_product {
      border-bottom: 1px solid #b2b2b2;
      padding-bottom: 4px;
      margin-bottom: 1px;
      .choosed_product_pic {
        width: 62px;
        height: 54px;
        float: left;
        text-align: center;
        margin-right: 8px;
        padding: 2px;
        img {
          display: inline-block;
          max-height: 100%;
          max-width: 100%;
        }
      }
      .choosed_product_text {
        width: 450px;
        float: left;
        padding-top: 8px;
        p {
          font-size: 18px;
          color: @h1_color;
          line-height: 1;
        }
        span {
          font-size: 14px;
          color: #383838;
        }
      }
      .choosed_product_act {
        width: 36px;
        float: right;
        margin-top: 10px;
        .checked, .checked_plus {
          width: 36px;
          height: 36px;
          background: url(../img/icons/checked2.png);
          cursor: pointer;
          position: relative;
          span {
            display: none;
            position: absolute;
            background: #333333;
            font-size: 11px;
            color: #E5E5E5;
            left: 20px;
            top: 10px;
            padding: 2px 4px;
          }
          &:hover span {
            display: inline-block;
          }
        }
        .checked_plus {
          background: url(../img/icons/checked3.png);
        }
      }
    }
  }
}

//\\ End Choosed list


// Generator Form

.generator_form {
  padding: 43px 5% 69px;
  margin-bottom: 104px;
  .text_fields {
    border-bottom: 1px solid #999999;
    padding-bottom: 62px;
    margin-bottom: 46px;
    .field {
      .text_field {
        font-family: mr, sans-serif;
        font-size: 20px;
        height: 32px;
        width: 76.95%;
        float: left;
        border-bottom: 1px dotted #000000;
        background: none;
        margin-right: 3.3%;
      }
      span {
        display: inline-block;
        line-height: 1;
        padding-top: 12px;
        font-size: 21px;
        color: #383838;
        max-width: 19%;

      }
    }
  }
  .radios {
    margin-bottom: 52px;
    display: table;
    width: 100%;
    .radios_section {
      display: table-cell;
      padding-top: 20px;
      &.radios_section1 {
        width: 32.8%;
        border-right: 1px solid #999999;
      }
      &.radios_section2 {
        width: 34.4%;
        border-right: 1px solid #999999;
      }
      &.radios_section2,
      &.radios_section3 {
        padding-left: 19px;
      }

      .radios_section_row {
        margin-bottom: 2px;
        .inputs {
          width: 64px;
          float: left;
          line-height: 20px;
          text-align: right;
          margin-right: 11px;
        }
        .radio {
          vertical-align: middle;
          display: inline-block;
        }
        .text {
          display: block;
          border: 1px solid #999999;
          max-width: 100%;
          height: 21px;
        }
      }
      label {
        font-size: 18px;
        color: #383838;
        line-height: 17px;
      }
    }
  }
  .buttons {
    width: 700px;
    margin: 0 auto;
    .btn {
      line-height: 36px;
      height: 36px;
      font-size: 16px;
      text-transform: uppercase;
      padding: 0 0;
      width: 270px;
      text-align: center;
      font-family: mr, "Myriad Pro", Arial, sans-serif;
    }
    .excel_btn {
      float: left;
    }
    .pdf_btn {
      float: right;
      i {
        display: inline-block;
        width: 27px;
        height: 28px;
        background: url(../img/icons-s51a623a2b8.png) -40px -560px;
        margin-right: 7px;
        vertical-align: middle;
      }
    }
  }
}

//\\ End Generator Form
///////////////End catalog Page

.lf {
  font-size: 42px;
  font-weight: 100;
  text-transform: none;
}

.toggle_link {
  display: table;
  font-size: 14px;
  color: #B2B2B2;
  text-decoration: none;
  text-align: center;
  margin: 0 auto;
  span {
    display: block;
    margin-bottom: 15px;
  }
  i {
    display: block;
    margin: 0 auto;
    width: 36px;
    height: 30px;
    background: url(../img/icons-s51a623a2b8.png) 80px -892px;
  }
  &.active {
    span {
      display: none;
    }
    i {
      background: url(../img/icons-s51a623a2b8.png) 40px -892px;
    }
  }
}
blockquote {
  .orange_border();
  font-size: 24px;
  text-align: center;
  width: 536px;
  line-height: 29px;
  padding: 33px 50px 40px;
  margin-top: 45px;
  margin-bottom: 40px;
  margin-right: 38px;
}
.mb_block {
  margin-bottom: 58px;
}
//////////////Appeal

.appeal {
  position: relative;
  .appeal_image {
    img {
      display: block;
    }
  }
  .appeal_content {
    padding-bottom: 53px;
    border-top: 0;
    border-radius: 0 0 6px 6px;
    article {
      .std_content_paddings();
      padding-left: 430px;
    }
    h1 {
      .lf();
      width: 250px;
      color: @h1_color;
      line-height: 1;
      float: left;
      margin-left: -288px;
      margin-bottom: 36px;
      margin-top: -3px;
      letter-spacing: -3px;
    }
    p {
      font-size: 16px;
      line-height: 19px;
      margin-bottom: 16px;
    }
    blockquote {
      margin-left: -288px;
      float: left;
      font-weight: 100;
    }
  }
  &.toggled {
    .appeal_content {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      color: #ffffff;
      background: @transparent_black;
      width: 33.15%;
      border: 0 none;
      box-shadow: 0 0 0;
      border-radius: 0;
      article {
        padding: 57px 70px 100px 36px;
      }
      h1 {
        float: none;
        width: auto;
        color: #ffffff;
        margin-left: 0;
      }
    }
  }
}
.toggled {
  .hided_content {
    display: none;
  }
}

///////////////End Appeal

////////////About Project

.about_project {
  .h2 {
    margin-bottom: 0;
  }
  .about_project_content {
    padding-bottom: 53px;
    .std_content_paddings();
    border-radius: 0 0 6px 6px;
    font-size: 16px;
    border-top: 0;
    article {
      padding-right: 25.7%;
      margin-bottom: 34px;
    }
    h1 {
      color: @h1_color;
      text-transform: uppercase;
      font-size: 36px;
      margin-top: 47px;

    }
    h2 {
      font-weight: normal;
      font-size: 22px;
      line-height: 26px;
      margin-bottom: 13px;
      margin-top: 14px;
    }
    p.selected {
      .orange_border();
      padding: 18px 0 19px;
      strong {
        color: #FF4A15;
      }
    }
    p {
      margin-bottom: 12px;
      line-height: 19px;
    }
    ul {
      li {
        list-style: none;
        line-height: 19px;
        margin-bottom: 12px;
        &:before {
          content: "• ";
        }
      }
    }
  }
}

///////////End About Project

/////////// Baners

