////////// End Baners

////////// EventSlider

.eventslider {
  padding: 0 5.14%;
  position: relative;
  margin-top: 20px;
  .eventslider__in {
    .eventslider__slides {
      .cycle-carousel-wrap {
        >div {
          margin-right: 40px;
          .item {
            position: relative;
            img {
              display: block;
            }
            div {
              position: absolute;
              left: 0;
              right: 0;
              bottom: 0;
              background: @transparent_black;
              color: #ffffff;
              max-height: 100%;
              white-space: normal;
              padding: 31px 28px 20px 36px;
              h1 {
                line-height: 1;
                font-size: 30px;
                letter-spacing: -1px;
                margin-bottom: 19px;
              }
              p {
                font-size: 16px;
                line-height: 19px;
              }
            }
          }
        }
      }
    }
  }
  .eventslider__prev, .eventslider__next {
    position: absolute;
    width: 5.14%;
    top: 0;
    bottom: 0;
    left: 0;
    cursor: pointer;
    i {
      background: url('../img/icons-s51a623a2b8.png') 0 -713px no-repeat;
      width: 20px;
      height: 37px;
      display: block;
      position: absolute;
      top: 50%;
      left: 0;
      margin-top: -18px;
    }
  }
  .eventslider__next {
    right: 0;
    left: auto;
    i {
      background: url('../img/icons-s51a623a2b8.png') 0 -478px no-repeat;
      left: auto;
      right: 0;
    }
  }
}

///////// End EvenSlider

